header {
  background-color: #EEFFBF;
}

.page-changer {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 80%;
  margin: .5rem auto 2rem auto;
}

.page-changer nav {
  align-self: center;
}

.user-page-container {
  width: 80%;
  margin: 0 auto 0 auto;
}

.user-page-container-info {
  width: 100%;
}

.page-changer h1 {
  display: block;
  font-size: 45px;
}

@media screen and (max-width: 1300px) {
  .user-page-container {
    width: 95%;
  }
}