.acc-info {
  width: 75%;
  margin: 0 auto 0 auto;
}

.base-info {
  display: flex;
  justify-content: space-between;
  font-size: 2rem;
  margin-top: 4rem;
  align-items: center;
}

.money-opers {
  display: flex;
  justify-content: space-around;
  padding-bottom: 8rem;
}

.history {
  font-size: 4rem;
}

@media screen and (max-width: 1500px) {
  .acc-info {
    width: 85%;
  }
}

@media screen and (max-width: 1200px) {
  .acc-info {
    width: 95%;
  }
}