.transaction {
  display: flex;
  width: 100%;
  font-size: 1.5rem;
  flex-direction: row;
  justify-content: space-around;
  padding: 1rem 1rem 1rem 1rem;
  box-sizing: border-box;
  border-radius: 10px;
  align-items: center;
}

@media screen and (max-width: 1300px) {
  .transaction {
    font-size: 1.2rem;
  }
}