.account {
  width: 100%;
  display: flex;
  padding: 1rem 1rem 1rem 1rem;
  box-sizing: border-box;
  font-size: 1.5rem;
  border-radius: 10px;
  cursor: pointer;
}

.account-type {
  width: 25%;
  display: flex;
  align-items: center;
}

.account-id {
  flex-grow: 1;
  display: flex;
  align-items: center;
}

.account-balance {
  width: 20%;
  display: flex;
  align-items: center;
}

.button-place {
  width: 10%;
}

@media screen and (max-width: 1200px) {
  .button-place {
    width: 20%;
    display: flex;
    justify-content: center;
  }
}